import { Container, Main, Row, Col, Button, Input, Breadcrumb, BreadcrumbItem,
    Table, TableColumn, Select, Option, Dialog, Form, FormItem, Alert } from 'element-ui';

export default {
    components: {
        'el-container': Container,
        'el-main': Main,
        'el-col': Col,
        'el-row': Row,
        'el-button': Button,
        'el-input': Input,
        'el-breadcrumb': Breadcrumb,
        'el-breadcrumb-item': BreadcrumbItem,
        'el-select': Select,
        'el-option': Option,
        'el-table': Table,
        'el-table-column': TableColumn,
        'el-dialog': Dialog,
        'el-form': Form,
        'el-form-item': FormItem,
        'el-alert': Alert
    },
    data() {
        return {
            mode: 'add',
            menus: [],
            level_one_menus: [],
            dialogFormVisible: false,
            add_form: {
                parent_id: '',
                name: '',
                type: '',
                url: '',
                key_word: ''
            },
            types: [],
            isView: false,
            isKeyWord: false,
            formLabelWidth: '120px',
        }
    },
    methods: {
        //发布按钮
        issueMenus: function() {
            axios.post('/api/wechat/menu/issueMenus').then(response => {
                this.$message.success(response.data.message);
            }).catch(error => {
                this.$message.error(error.response.data.message);
            });
        },
        //获取所有按钮
        getMenus: function() {
            axios.post('/api/wechat/menu/getMenus').then(response => {
                this.menus = response.data.data;
            }).catch(error => {
                this.$message.error(error.response.data.message);
            });
        },
        //获取一级按钮
        getLevelOneMenus: function() {
            axios.post('/api/wechat/menu/getLevelOnes').then(response => {
                this.level_one_menus = response.data.data;
            }).catch(error => {
                this.$message.error(error.response.data.message);
            });
        },
        showModifyMenu: function(menu) {
            this.add_form = {
                id: menu.id,
                parent_id: menu.parent_id,
                name: menu.name,
                type: menu.type,
                url: menu.url,
                key_word: menu.key_word
            };
            this.mode = 'modify';
            this.dialogFormVisible = true;
        },
        modifyMenu: function() {
            axios.post('/api/wechat/menu/modify', this.add_form).then(response => {
                    this.$message.success(response.data.message);
                    this.getMenus();
                    this.dialogFormVisible = false;
            }).catch(error => {
                this.$message.error(error.response.data.message);
            });
        },
        showAddMenu: function(show) {
            this.getLevelOneMenus();
            this.formatAddForm();
            this.dialogFormVisible = show;
            this.mode = 'add';
        },
        addMenu: function() {
            this.dialogFormVisible = false;
            axios.post('/api/wechat/menu/add', this.add_form).then(response => {
                    this.$message.success(response.data.message);
                    this.getMenus();
            }).catch(error => {
                this.$message.error(error.response.data.message);
            });
        },
        delMenu: function(id) {
            axios.post('/api/wechat/menu/del', {id:id}).then(response => {
                    this.$message.success(response.data.message);
                    this.getMenus();
            }).catch(error => {
                this.$message.error(error.response.data.message);
            });
        },
        //改变按钮排序
        modifyIndex: function(parent_id, sort_id, direction) {
            let data = {parent_id: parent_id, sort_id: sort_id, direction: direction};
            axios.post('/api/wechat/menu/modifySortId', data).then(response => {
                this.getMenus();
            });
        },
        formatAddForm() {
            this.add_form = {
                parent_id: null,
                name: null,
                type: null,
                url: null,
                key_word: null
            };
        }
        //添加按钮页面中的类型判断
        /*initType: function(event) {
         if (event == 0) {
         this.types = [
         {value:'button', label:'无事件的一级菜单'},
         {value:'view', label:'超链接'},
         {value:'click', label:'关键字回复'},
         ];
         } else {
         this.types = [
         {value:'view', label:'超链接'},
         {value:'click', label:'关键字回复'},
         ];
         }
         },*/
        /*changeType: function(event) {
         if (event == 'view') {
         this.isView = true;
         this.isKeyWord = false;
         } else if (event == 'click') {
         this.isView = false;
         this.isKeyWord = true;
         }
         }*/
    },
    watch: {
        'add_form.parent_id': function(val) {
            if (val == 0) {
                this.types = [
                    {value:'button', label:'无事件的一级菜单'},
                    {value:'view', label:'超链接'},
                    {value:'click', label:'关键字回复'},
                ];
            } else if(val ==  null) {
                this.types = [];
            } else {
                this.types = [
                    {value:'view', label:'超链接'},
                    {value:'click', label:'关键字回复'},
                ];
            }
        },
        'add_form.type': function (val) {
            if (val == 'view') {
                this.isView = true;
                this.isKeyWord = false;
            } else if (val == 'click') {
                this.isView = false;
                this.isKeyWord = true;
            } else {
                this.isView = false;
                this.isKeyWord = false;
            }
        }
    },
    mounted: function() {
        this.$nextTick(function() {
            this.getMenus();
            // this.getLevelOneMenus();
        });
    }
}



